import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';
import { ListSubheaderCharacter, ListItemCharacter,ListSubheaderMemoria, ListItemMemoria } from './DataListSub'

const listItemStyle = (index) => ({
  border: '1px solid lightgray',
  borderTop: index > 0 ? 'none' : '1px solid lightgray',
  borderLeft: 'none',
  borderRight: 'none',
  width: '100%',
  height: 'auto',
  backgroundColor: '#596782',
  padding: 0,
});

const listSubheaderStyle = {
  backgroundColor: '#596782',
  height: 'auto',
  color: 'white',
  zIndex: 1,
  padding: 0,
  margin: 0,
  lineHeight: 1,
}

export const DataList = ({ rows, category, maxValues, filterItems, sortItem }) => {

  return (
    <List
      sx={{ width: '100%' }}
      subheader={
        <ListSubheader sx={listSubheaderStyle}>
          {category === 'character'
            ? <ListSubheaderCharacter />
            : <ListSubheaderMemoria />
          }


        </ListSubheader>
      }
    >
      {rows.map((dataEl, index) => (
        <ListItem key={dataEl.name} sx={listItemStyle(index)} >

          {category === 'character'
            ? <ListItemCharacter
              category={category}
              dataEl={dataEl}
              maxValues={maxValues}
              filterItems={filterItems}
              sortItem={sortItem}
            />
            : <ListItemMemoria
              category={category}
              dataEl={dataEl}
              maxValues={maxValues}
              filterItems={filterItems}
              sortItem={sortItem}
            />}

        </ListItem>
      ))}
    </List>

  )
}