import React, { useState, useEffect } from 'react';
import { Chip, Grid, List, ListItem, ListSubheader, Stack, Tooltip } from '@mui/material';

import { character } from '../constants/character';
import { memoria } from '../constants/effects';
import { DataList } from './DataList';

import { filterList, filterItemitialize, FilterButtonGroup, IsFilterPassed } from './Filter'
import { SortButton, SortButtonGroup } from './Sort'

const sortItemList = {
  character: ['date', 'HP', '素早さ', '物攻', '物防', '魔攻', '魔防', 'damage0', 'break0', 'heal0', 'wait0', 'damage1', 'break1', 'heal1', 'wait1', 'damage2', 'break2', 'heal2', 'wait2'],
  memoria: ['date', 'HP', '素早さ', '物攻', '物防', '魔攻', '魔防'],
}



const categoryMap = {
  character: character,
  memoria: memoria,
}

export const TabContents = ({ category, maxValues }) => {

  const [filterItems, setFilterItems] = useState(filterItemitialize(category));
  const [sortItem, setSortItem] = useState('date');
  const [sortOrder, setSortOrder] = useState('desc');

  useEffect(() => {
    let newRows = [];
    Object.entries(categoryMap[category]).forEach(([dataName, dataEl]) => {
      if (IsFilterPassed({ category, dataEl, filterItems })) {
        newRows.push({
          ...dataEl,
          ...dataEl.status,
          name: dataName,
        })
      }
    })

    newRows.sort((a, b) => {
      const itemA = sortItem === 'date' ? new Date(a[sortItem]) : a[sortItem];
      const itemB = sortItem === 'date' ? new Date(b[sortItem]) : b[sortItem];
      if (sortOrder === 'asc') {
        if (itemA < itemB) return -1; // aがbより前にくる場合
        if (itemA > itemB) return 1;  // bがaより前にくる場合
      } else { // descの場合
        if (itemA < itemB) return 1;  // bがaより前にくる場合
        if (itemA > itemB) return -1; // aがbより前にくる場合
      }
      return 0;  // aとbが同じ場合
    });

    setRows(newRows);
  }, [filterItems, sortItem, sortOrder]);

  const [rows, setRows] = useState([]);

  return (
    <div style={{ padding: '4px' }}>
      <Grid container >
        {Object.keys(filterList[category]).map(group => (
          <Grid item key={group}>
            <FilterButtonGroup
              category={category}
              filterItems={filterItems}
              setFilterItems={setFilterItems}
              group={group}
            />
          </Grid>
        ))}
      </Grid>
      <Grid container>
        <Grid item>
          <SortButtonGroup props={{ category, sortItem, setSortItem, sortItemList: sortItemList[category] }} />
        </Grid>
        <Grid item>
          <SortButton props={{ sortOrder, setSortOrder }} />
        </Grid>
      </Grid>


      <DataList
        rows={rows}
        category={category}
        maxValues={maxValues}
        filterItems={filterItems}
        sortItem={sortItem}
      />



    </div>
  );
};
